@import '../../styles/variables.css';

.advertWrap{
    width: 100%;
    min-height: 100%;  
}

.imageWrap{
    border-radius: 14px;
    width: 461px;
    height: 248px;
    max-width: 100%;
    overflow: hidden;
       
    margin-bottom: 14px;
    background-color: var(--image-bg-color);
}

.imageCar{
    object-fit: cover;
    width: 100%;
}


.titleWrapper{
    font-size: 18px;
    font-weight: 500;
    line-height: 1.3333;
    color: var(--primary-color);
    margin-bottom: 8px;
}

.textAccent{
    color: var(--accent-color);
}

.info{
    color: var(--secondary-color);
    font-size: 12px;
    line-height: 1.5;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;

}

.textSeparator{
    color: var(--separator-color);
}
.description{
    color: var(--primary-color);
    font-size: 14px;
    line-height: 1.42857;
    margin-top: 14px;
    

}

.title{
    color: var(--primary-color);
    font-size: 14px;
    font-weight: 500;
    line-height: 1.42857;
    margin-bottom: 8px;
    margin-top: 24px;
}
.conditionsWrapper{
    display: flex;
    gap: 8px;
    align-items: center;
    margin-bottom: 8px;
}
.conditionBg{
    padding: 7px 14px;
    border-radius: 35px;
    background-color: var(--condition-bg-color);
    color: var(--condition-color);
    font-size: 12px;
    line-height: 1.5;
}

.conditionsAccent{
    color: var(--accent-color);
    font-size: 12px;
    font-weight: 600;
    line-height: 1.5;
}
.advertListItemButton{
    background-color: var(--accent-color);
    color: var(--invert-color);
    transition: background-color 250ms ease-in-out;
    width: 100%;
    border-radius: 12px;
    border: none;
    padding: 12px 0;
    margin-top: 28px;
    cursor: pointer;
  
    &:hover {
      background-color: var(--accent-hover);
    }
}