@import '../../styles/variables.css';

.advertWrap{
    width: 100%;
    display: flex;
    flex-direction: column;
    min-height: 100%;
    justify-content: space-between;
}

.imageWrap{
    border-radius: 14px;
    width: 274px;
    height: 268px;
    max-width: 100%;
    overflow: hidden;
    position: relative;
    transition: transform 250ms ease-in-out, box-shadow 250ms ease-in-out;
    margin-bottom: 14px;
      
    /* &:hover {
      transform: scale(1.05);
      box-shadow: 0 1px 10px black;
    } */
  
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        180deg,
        rgba(18, 20, 23, 0.5) 2.5%,
        rgba(18, 20, 23, 0) 41.07%
      ); 
    }

}


.iconWrap{
    position: absolute;
    top: 14px;
    right: 14px;
    width: 20px;
    height: 20px;
    cursor: pointer;
    transition: transform 250ms ease-in-out;
  
    &:hover {
      transform: scale(1.5);
    }
}

.imageCar{
    object-fit: cover;
    width: 100%;
}


.titleWrapper{
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--primary-color);
    margin-bottom: 8px;
}

.textAccent{
    color: var(--accent-color);
}

.info{
    color: var(--secondary-color);
    font-size: 12px;
    line-height: 1.5;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;

}

.textSeparator{
    color: var(--separator-color);
}

.advertListItemButton{
    background-color: var(--accent-color);
    color: var(--bg-color);
    transition: background-color 250ms ease-in-out;
    width: 100%;
    border-radius: 12px;
    border: none;
    padding: 12px 0;
    margin-top: 28px;
    cursor: pointer;
  
    &:hover {
      background-color: var(--accent-hover);
    }
}








